import logo from "./logo.svg";
import "./App.css";
import { useEffect } from "react";
import chan from "./qwebchannel.js";

//initialize
window.onload = () => {
    if (!window.qt || !window.qt.webChannelTransport) {
        return;
    }
    new chan.QWebChannel(window.qt.webChannelTransport, function (channel) {
        window.Python = channel.objects.Python;
    });
};
//
function App() {
    useEffect(() => {}, []);

    return (
        <div className="App">
            <script language="javascript" src="/qwebchannel.js"></script>
            <header className="App-header">
                <button
                    onClick={(e) => {
                        if (window.Python) {
                            window.Python.runStatements("print(hou.selectedNodes())");
                        }
                    }}
                >
                    sdf
                </button>
            </header>
        </div>
    );
}

export default App;
